
ol,
ul {
  margin: 0;
  margin-bottom: 0px !important;
}
body {
  margin: 0;
  /*	font-family: 'Open Sans', sans-serif; */
  font-family: "Raleway", sans-serif !important;
  color: #555 !important;
}
button::-moz-focus-inner,
button:active,
button:focus {
  border: 0;
  outline: none;
}
a:hover {
  color: #e96443 !important;
  text-decoration: none !important;
  -webkit-transition: color 0.1s ease;
  transition: color 0.1s ease;
}
.material-icons {
  font-size: inherit !important;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}
input * {
  outline: none;
}
.clearfix {
  clear: both;
}
#main-container {
  position: relative;
  height: 100vh;
  margin-left: 80px;
  /*background-color: #e1e4e7 !important;*/
  /* #eaecee */
  background-color: transparent;
  background-image: url(data:image/gif;base64,R0lGODlhFAAQAIAAAL26tP///yH5BAAAAAAALAAAAAAUABAAAAImDIKXaKzaloyMwusm1rzHbWVV6D0i+Z2gWZ7qK62w3NEojLJ5VgAAOw==);
  background-repeat: repeat;
  background-attachment: scroll;
  background-clip: border-box;
  background-origin: padding-box;
  background-size: auto auto;
  overflow: hidden;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-leave-active {
  position: absolute !important;
  width: 99%;
}
.fade-move {
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
