#sidebar {
	position: absolute;
	left: 0px;
	height: 100vh;
	width: 80px;
	background-color: #555;
	z-index: 10;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	-webkit-box-shadow: 3px 0px 4px 0px rgba(0, 0, 0, 0.3);
	        box-shadow: 3px 0px 4px 0px rgba(0, 0, 0, 0.3);
	color: #eee;
	overflow: auto;
}
#sidebar.open {
	width: 230px;
	padding: 0 20px;
}
#sidebar .logo-cont {
	position: relative;
	display: block;
	height: 50px;
	border-radius: 10px;
	background-color: #eee;
	margin: 20px 10px 12px 10px;
	cursor: pointer;
	-webkit-box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
	        box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
#sidebar .logo-cont::after {
	content: "";
	position: absolute;
	bottom: -11px;
	height: 2px;
	width: 40px;
	left: 10px;
	background-color: #e1e4e7;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
#sidebar.open .logo-cont::after {
	width: 190px;
}
#sidebar .server {
	width: 60px;
	height: 60px;
	border-radius: 30px;
	background-color: #eee;
	position: relative;
	margin: 10px 10px;
	-webkit-box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
	        box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
	float: left;
}
#sidebar .server-name {
	padding-top: 25px;
	float: left;
	width: 130px;
}
#sidebar .server-users {
	float: left;
	width: 130px;
}
#sidebar .server-cont:hover .server,
#sidebar .logo-cont:hover,
#sidebar .addNew-btn:hover {
	-webkit-box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.5);
	        box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.5);
}
#sidebar .server-cont {
	position: relative;
	font-size: 13px;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	height: 80px;
	overflow: hidden;
}
#sidebar .server-cont span {
	padding: 0 2px;
}
#sidebar .server-cont::after {
	content: "";
	position: absolute;
	bottom: 0px;
	height: 2px;
	width: 40px;
	left: 20px;
	background-color: #e1e4e7;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
#sidebar.open .server-cont::after {
	width: 190px;
}
#sidebar .server-cont .notifications {
	position: absolute;
	top: 50px;
	left: 52px;
	width: 18px;
	height: 18px;
	border-radius: 50%;
	background-color: lightcoral;
	text-align: center;
	line-height: 16px;
	color: #eee;
	font-size: 13px;
}
#sidebar .addNew-btn {
	height: 60px;
	border-radius: 30px;
	background-color: #eee;
	position: relative;
	display: block;
	margin: 10px 10px;
	cursor: pointer;
	-webkit-box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
	        box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
	color: #555;
	font-size: 46px;
	text-align: center;
	-webkit-transition: all 0.2s ease;
	transition: all 0.2s ease;
}
#sidebar.open .addNew-btn {
	height: 40px;
	font-size: 26px;
}
#sidebar .addNew-btn > * {
	padding: 7px;
}


