.member-list .profile .user-image {
  width: 300px;
  height: 300px;
  border-radius: 10px;
  background-color: #eee;
  position: absolute;
  right: 70px;
  top: -70px;
  -webkit-box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.3);
          box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.3);
  overflow: hidden;
}
.member-list .profile .user-image img {
  width: 300px;
  height: 300px;
}
.memberListSlide-enter-active,
.memberListSlide-leave-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.memberListSlide-enter,
.memberListSlide-leave-to {
  opacity: 0;
  -webkit-transform: translateX(50px);
          transform: translateX(50px);
}

.pre-loader-cont {
	width: 100vw;
	height: 100vh;
	z-index: 1000;
	position: fixed;
	background-color: #222;
}
.pre-loader-cont .enter-btn {
	position: absolute;
	height: 50px;
	width: 260px;
	border: 2px solid #f08080;
	border-radius: 25px;
	top: 45%;
	left: 0;
	right: 0;
	margin: auto;
	text-align: center;
	color: #f08080;
	line-height: 50px;
	font-size: 30px;
	letter-spacing: 10px;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
}
.pre-loader-cont .enter-btn:hover {
	letter-spacing: 15px;
}
.pre-loader-cont .logout-btn {
	position: absolute;
	height: 40px;
	width: 100px;
	border: 2px solid #f08080;
	border-radius: 25px;
	top: 10%;
	left: 5%;
	text-align: center;
	color: #f08080;
	line-height: 40px;
	font-size: 16px;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
}


.spinner {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100vh;
  width: 100vw;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
